<section class="registration"><div class="container"><div class="registration-wrap"><form class="registration__form" action="#"> <div class="registration__form-avatar"><img :src="require('@/assets/img/' + registration.avatarImg)"></div><h2 class="registration__form-title" v-html="registration.title"></h2><div class="registration__form-input-row"><main-input v-for="item in registration.input" :key="item.id" :elId="item.id" :elType="item.type" :elClass="item.class" :elValue="item.value" :elPlaceholder="item.placeholder" @inputAction="registrationinputSave(item.id)"></main-input><main-btn :elText="registration.btnText" elClass="registration__form-btn" @clickAction="registerPerson()"></main-btn></div><p class="registration__form-text"><span>{{ registration.question }}</span><button class="title-gradient" type="button" @click.once="(registration.animSection = false), animRegistration(), loginToRouter()">Login...</button></p></form></div></div></section>